import React, { Component } from 'react';
import PropTypes from 'prop-types';

import Data from './data.json';
import Templates from './templates.json';

import './Main.css';

class Main extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoaded: false,
    }
  }
  componentDidMount() {
    const obj = {};
    Templates.templates.forEach((item) => {
      item.items.forEach((o) => {
        obj[o] = false;
      });
    })
    this.setState({
      ...obj,
      isLoaded: true,
    })
  }
  goToDetail = (obj) => {
    const { history } = this.props;
    const params = {
      pathname: `/detail/${obj.pid_device}`,
      state: {
        device_name: obj.device_name,
        device_uri: obj.device_uri,
      }
    }
    return history.push(params)
  }

  handleChange = (e) => {
    const { target } = e;
    const { name } = target;
    if (this.state[name]) {
      this.setState({ [name]: false });
    } else {
      this.setState({ [name]: true });
    }
  }

  renderSideBar = () => {
    const { templates } = Templates;
    const List = templates.map((item) => {
      const items = item.items.map((o) => (
        <li>
          <label className="sidebar-item">
            {o}
            <input
              type="checkbox"
              name={o}
              value={this.state[o] ? this.state[o] : false}
              onChange={this.handleChange}
            />
          </label>
        </li>
      ))
      return (
        <div>
          <div>{item.label}</div>
          <ul>
            {items}
          </ul>
        </div>
      );
    });
    return List;
  }

  renderSection = () => {
    const { templates } = Templates;
    const { data } = Data;
    const filterList = [];
    const filterSection = [];
    const filterBrand = [];
    const filterPlatform = [];
    templates.forEach((item, index) => {
      item.items.forEach((str) => {
        if (this.state[str]) {
          filterList.push(str);
          if (index === 0) {
            filterSection.push(str);
          }
          if (index === 1) {
            filterBrand.push(str);
          }
          if (index === 2) {
            filterPlatform.push(str);
          }
        }
      })
    });
    if (filterList.length === 0) {
      const List = templates[0].items.map((item) => {
        const items = data.filter((o) => {
          return o.category === item;
        }).map((o) => {
          return (
            <div onClick={() => { this.goToDetail(o) }}>
              <div className="img-frame" style={{backgroundImage:`url(${o.device_uri})`}} />
              <div>{o.device_name}</div>
            </div>
          )
        })
        return (
          <div>
            <h3>{item}</h3>
            <div className="section-items">
              {items}
            </div>
          </div>
        )
      })
      return List;
    } else {
      let filterTemplate = [];
      if (filterSection.length === 0) {
        filterTemplate = templates[0].items;
      } else {
        filterTemplate = filterSection.filter((str) => {
          return this.state[str];
        })
      }
      const filterData = data.filter((obj) => {
        let isFilter = false;
        const isFindBrand = filterList.find((str) => {
          return str === obj.brand;
        });
        const isFilterPlatform = filterList.find((str) => {
          return str === obj.platform;
        });
        if (filterBrand.length === 0) {
          if (filterPlatform !== undefined) {
            isFilter = true;
          }
        } else if (filterPlatform.length === 0) {
          if (isFindBrand !== undefined) {
            isFilter = true;
          }
        } else {
          if (isFindBrand !== undefined && isFilterPlatform !== undefined) {
            isFilter = true;
          }
        }
        return isFilter;
      })
      const List = filterTemplate.map((str) => {
        const items = filterData.filter((o) => {
          return o.category === str;
        }).map((o) => {
          return (
            <div>
              <div className="img-frame" style={{backgroundImage:`url(${o.device_uri})`}} />
              <div>{o.device_name}</div>
            </div>
          )
        })
        console.log(str);
        return (
          <div>
            <h3>{str}</h3>
            <div className="section-items">
              {items}
            </div>
          </div>
        )
      });
      return List;
    }
  }

  render() {
    const { isLoaded } = this.state;
    if(!isLoaded) return null;
    return (
      <div className="wrapper">
        <div className="sidebar">
          {this.renderSideBar()}
        </div>
        <div className="container">
          {this.renderSection()}
        </div>
      </div>
    );
  }
}

Main.propTypes = {
  history: PropTypes.object,
};

Main.defaultProps = {
  history: null,
};

export default Main;